body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

*::-webkit-scrollbar {
    width: 0;
}

*::-moz-selection {
	color: #409EFF;
	background: transparent;
}

*::selection {
	color: #409EFF;
	background: transparent;
}

@-webkit-keyframes up {
	0% {
		opacity: 0;
		-webkit-transform: translateY(50px);
		        transform: translateY(50px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes up {
	0% {
		opacity: 0;
		-webkit-transform: translateY(50px);
		        transform: translateY(50px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@-webkit-keyframes down {
	0% {
		opacity: 0;
		-webkit-transform: translateY(-50px);
		        transform: translateY(-50px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes down {
	0% {
		opacity: 0;
		-webkit-transform: translateY(-50px);
		        transform: translateY(-50px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@-webkit-keyframes color {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes color {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.App {
  height: 100%;
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

#header {
	height: 4rem;
	padding: .5rem 0;
	box-shadow: 0 0 12px 0 rgba(7, 17, 27, .06);
	-webkit-animation: down 1s;
	        animation: down 1s;
}

#nav {
	margin: 0 auto;
	max-width: 1000px;
}

#logo {
	height: 3rem;
	padding: .4rem .5rem .6rem 0;
	float: left;
}

#title {
	font-size: 20px;
	font-weight: bold;
	font-family: Trebuchet MS;
	line-height: 3rem;
	float: left;
}

#btn {
	padding: calc((3rem - 40px) / 2) 0;
	float: right;
}
#content {
	min-height: calc(100% - 10rem);
	padding: 10px 0 20px;
	line-height: 2;
	-webkit-animation: up 1s;
	        animation: up 1s;
}

.ant-table-wrapper {
	width: 1000px;
	margin: 20px auto;
}

.ant-table {
	border-radius: 8px;
	box-shadow: 0 4px 8px 6px rgba(7, 17, 27, .06);
	-webkit-transition: .3s;
	transition: .3s;
}

.ant-table:hover {
	box-shadow: 0 4px 12px 12px rgba(7, 17, 27, .15);
}

#table-header .ant-input-prefix i {
	color: rgba(0, 0, 0, .25);
}

#table-header .ant-input-affix-wrapper {
	width: 150px;
	float: left;
	margin-left: 15px;
}

#table-header .ant-btn {
	margin-left: 215px;
}

th.column-left,
td.column-left {
	padding-left: 20px !important;
	padding-right: 0 !important;
}

th.column-center,
td.column-center {
  text-align: center !important;
}
#footer {
	height: 6rem;
	padding: 1.5rem 0;
	background-size: 200%;
	background-image: -webkit-gradient(
		linear,
		left top, right top,
		from(#bb313e25),
		color-stop(#bb313e25),
		color-stop(#d7222925),
		color-stop(#dd4a1625),
		color-stop(#e4761525),
		color-stop(#f5c50025),
		color-stop(#f0e92725),
		color-stop(#b1ce2425),
		color-stop(#48a93525),
		color-stop(#03944525),
		color-stop(#157c4f25),
		color-stop(#176a5825),
		color-stop(#1b556325),
		color-stop(#1d386f25),
		color-stop(#1d386f25),
		color-stop(#20277825),
		color-stop(#52266325),
		to(#8a244b25)
	);
	background-image: linear-gradient(
		to right,
		#bb313e25,
		#bb313e25,
		#d7222925,
		#dd4a1625,
		#e4761525,
		#f5c50025,
		#f0e92725,
		#b1ce2425,
		#48a93525,
		#03944525,
		#157c4f25,
		#176a5825,
		#1b556325,
		#1d386f25,
		#1d386f25,
		#20277825,
		#52266325,
		#8a244b25
	);
	-webkit-animation: up 1s, color 5s infinite;
	        animation: up 1s, color 5s infinite;
}

#footer a {
	color: #555;
	text-decoration: none;
	-webkit-transition: .2s;
	transition: .2s;
}

#footer a:hover {
	color: #409EFF;
}

#copyright,
#framework {
	color: #555;
	line-height: 2;
	font-size: 13.5px;
	font-family: Verdana;
	text-align: center;
}
